.App{
  min-height: 100vh;
  background-image: linear-gradient(to top, #8a5fa5 0%, #269c92 100%);
  color: white;
}

h1{
  text-align: center;
  padding: 20px;
}

form{
  min-height: 10vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

input{
  width: 30%;
  border: none;
  border-radius: 10px;
  padding: 10px;
}

button{
  border: none;
  border-radius: 10px;
  padding: 10px 20px;
  background: #b490ca;
  color: white;
  margin: 5px;
}

.recipe{
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
  margin: 20px;
}

img{
  height: 300px;
  width: 300px;
  border-radius: 8px;
}

li{
  width: 80%;
}

.recipes{
  width: 20%;
}